/*
==============================
Base styles
==============================
*/

// keyframes

@keyframes grow {
  0% {
    transform: scale(1);
  }
  20% {
    transform: scale(.8);
  }
  40% {
    transform: scale(1.1);
  }
  60% {
    transform: scale(.9);
  }
}

// Header

.card {
  @extend %card;
  @extend %m-top;
}

nav {
  @extend %center;
  position: relative;

  @include tablet {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

}

.svg-logo {
  fill: $color-accent;
}

.logo {
  width: em(56px);
  &:hover {
    animation: grow .5s ease-in-out;
  }
}


.main-nav {
  @extend %m-top;
  display: none;
  text-align: right;

  @include tablet {
    display: flex;
    margin: 0;
  }

  & a {
    @extend %p-5;
    display: block;
    color: $color-secondary;
    font-weight: $font-weight--bold;
    text-transform: capitalize;
    transition: color .3s ease-out;

    &:hover {
      color: $color-accent;
    }

    &:active {
      color: darken($color-accent, 20%);
    }
  }
}

.main-nav li {
  margin-bottom: em(10px);

  @include tablet {
    margin-bottom: 0;
    margin-left: em(20px);
  }
}

.nav-btn {
  position: absolute;
  top: em(15px);
  right: em(15px);
  width: 24px;
  height: 24px;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .5s ease-in-out;
  -moz-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  cursor: pointer;

  @include tablet {
    display: none;
  }
}

.nav-btn span {
  display: block;
  position: absolute;
  height: 2px;
  width: 100%;
  background: #1f1f1f;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .25s ease-in-out;
  -moz-transition: .25s ease-in-out;
  -o-transition: .25s ease-in-out;
  transition: .25s ease-in-out;
}

.nav-btn span:nth-child(1) {
  top: 0px;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}

.nav-btn span:nth-child(2) {
  top: 0.6875em;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}

.nav-btn span:nth-child(3) {
  bottom: 0;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}

.nav-btn.open span:nth-child(1) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
  top: 0.125em;
}

.nav-btn.open span:nth-child(2) {
  width: 0%;
  opacity: 0;
}

.nav-btn.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
  bottom: 0.1875em;
}

// Gallery

.gallery {
  @include tablet {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
}

.photo-container {
  perspective: 800px;
  margin: 0 0 em(25px) 0;

  @include tablet {
    flex-basis: 48.5%;
    margin-bottom: 2.5%;
  }
  @include desktop {
    flex-basis: 31.666666666666667%;
  }
}

.photo {
  @extend %b-shadow;
  position: relative;
  transform-style: preserve-3d;
  transition: transform 1s cubic-bezier(.55, -.62, .27, 1.2);
  transform-style: preserve-3d;
}

.photo:hover {
  transform: rotateY(-180deg);
}

.side-b {
  @extend %p-15;
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: $color-accent;
  transform: rotateY(180deg);

  @include tablet {
    padding: em(10px);
  }

}

.side-a,
.side-b {
  -webkit-backface-visibility: hidden;
}

.side-b p,
.side-b h3 {
  color: $white;
  text-align: center;
}

.side-b h3 {
  margin: em(10px) 0 0 0;
  @include desktop {
    margin-top: em(5px);
  }
}

.side-b p {
  margin: 0;

  @include tablet {
    font-size: .8rem;
  }
}
.photo-links {
  text-align: center;
  margin-top: em(5px);

  & a {
    display: inline-block;
    min-width: 100px;
    margin: em(10px) em(10px) 0 em(10px);
    padding: em(5px);
    background: darken($color-accent, 20%);
    border-radius: em(25px);
    text-transform: uppercase;
    transition: background .3s ease-out;

    @include tablet {
      font-size: .8rem;
    }

    &:hover {
      background: darken($color-accent,30%);
      color: $color-accent;
    }
  }
}

.photo-skills {
  display: flex;
  justify-content: flex-end;
  position: absolute;
  right: em(10px);
  bottom: em(10px);

  @include desktop {
    right: em(5px);
    bottom: em(5px);
  }
}

.photo-skills p {
  @include font(.8rem, 1.5,$font-weight--bold, darken($color-accent, 20%));
  text-transform: uppercase;
  margin: 0 0 0 em(5px);
  @include tablet {
    font-size: .6rem;
  }
}

// About section

.about-and-contact {
  @include tablet {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.flex-col {
  @include tablet {
    width: 65%;
    display: flex;
    flex-direction: column;
  }
}

#about,
#treehouse {
  flex: 1;
}

#contact {
  @include linearGradient($geyser, $catskill-white);
  background: url('#{$img-path}profile-bg.jpg') no-repeat bottom left, rgba(255, 255, 255, 0.9);
  background-blend-mode: overlay;
  text-align: center;
  @include tablet {
    width: 32.5%;
  }
}

.profile-pic {
  @extend %b-shadow;
  max-width: 125px;
  margin: em(15px) auto;
  border-radius: 50%;
  &:hover {
    animation: grow .6s ease-in-out;
  }
}

.social-icons {
  display: flex;
  justify-content: center;
  & li {
    margin: em(15px);
    width: em(32px);
    height: em(32px);
  }
}

.background-circle {
  fill: $color-primary;
  transition: fill .3s ease-out;
}

.social-icons svg:hover .background-circle {
  fill: $color-accent;
}

.social-icons svg:active .background-circle {
  fill: darken($color-accent, 15%);
}

.twitter,
.linkedin,
.github {
  fill: $color-background;
}

.contact-btn {
  display: block;
  width: 150px;
  margin: 0 auto em(20px);
  padding: em(10px);
  border-radius: em(25px);
  background: $color-btn;
  color: $white;
  text-transform: uppercase;
  transition: background .5s ease-out;
  &:hover {
    color: $white;
    background: darken($color-btn, 20%);
  }
}

// Treehouse section

#treehouse {
  background: $color-accent;
  & h1, p, span {
    color: $color-background;
  }
  & h1 {
    margin-top: 0;
  }
  & a {
    color: darken($color-accent, 20%);
    transition: color .3s ease-out;
    &:hover {
      color: darken($color-accent, 30%);
    }
  }
}

.flex-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

#treehouse svg {
  display: inline-block;
  width: 4rem;;
  height: 4rem;
  margin-top: em(-10px);
  margin-left: em(-15px);
}

.treehouse {
  fill: $color-background;
  width: 100%;
}

hr {
  border: 0;
  height: 1px;
  background-image: -webkit-linear-gradient(left, rgba(255,255,255,0), $catskill-white, rgba(255,255,255,0));
  background-image: -moz-linear-gradient(left, rgba(255,255,255,0), $catskill-white, rgba(255,255,255,0));
  background-image: -ms-linear-gradient(left, rgba(255,255,255,0), $catskill-white, rgba(255,255,255,0));
  background-image: -o-linear-gradient(left, rgba(255,255,255,0), $catskill-white, rgba(255,255,255,0));
}

.treehouse-stats {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.treehouse-stats p {
  @include font(em(52px), 1, $font-weight--bold, $color-background);
  position: relative;
  & span {
    font-size: 1rem;
    color: darken($color-accent, 20%);
    position: absolute;
    bottom: em(-20px);
    right: 0;
  }
}

.skills {
  text-align: right;
  width: 60%;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.skills span {
  @extend %b-radius;
  padding: em(5px);
  margin: em(5px);
  background: darken($color-accent, 20%);
  font-weight: $font-weight--bold;
  color: $color-accent;
}

// Footer

footer .card {
  background: $color-primary;
  display: flex;
  justify-content: space-between;
  align-items: center;
  & p {
    margin: 0;
    color: $catskill-white;
    text-align: center;
  }
  & a {
    color: $catskill-white;
    transition: color .3s ease-out;
    &:hover {
      color: lighten($catskill-white, 30%);
    }
  }
}
