/*
==============================
Global config
==============================
*/

// Font stacks

$font-url--google:  'https://fonts.googleapis.com/css?family=Hind:700|Open+Sans:400,700';

$font-family--primary: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
$font-family--secondary: 'Hind', $font-family--primary;

// // Font weights

$font-weight--regular: 400;
$font-weight--bold: 700;

// Base colors

$white:            #fff;
$sirocco:          #707e81;
$cod-gray:         #0d0d0d;
$emerald:          #64ce83;
$tomato:            #ff6347;

// Gradient colors
$geyser:           #cfd9df;
$catskill-white:   #e2ebf0;

// Color usage

$color-primary:    $sirocco;
$color-secondary:  $cod-gray;
$color-accent:     $emerald;
$color-btn:        $tomato;
$color-background: $white;
$color-shadow:     rgba(0,0,0,0.2);

// Text

$base__font-size: 16px;
$base__line-height: 24px;

//Paths

$img-path: '../img/';

// Media queries

$brk-tablet: 768px;
$brk-desktop: 1024px;
