/*
==============================
Helpers
==============================
*/

// Font style mixin

@mixin font($size, $l-height: $base__line-height, $weight: $font-weight--regular, $color: $color-secondary) {
  font-size: $size;
  line-height: $l-height;
  font-weight: $weight;
  color: $color;
}

// Linear gradient mixin

@mixin linearGradient($top, $bottom) {
  background: $geyser;
  background: -moz-linear-gradient(45deg, $geyser 0%, $catskill-white 100%);
  background: -webkit-gradient(left bottom, right top, color-stop(0%, $geyser), color-stop(100%, $catskill-white));
  background: -webkit-linear-gradient(45deg, $geyser 0%, $catskill-white 100%);
  background: -o-linear-gradient(45deg, $geyser 0%, $catskill-white 100%);
  background: -ms-linear-gradient(45deg, $geyser 0%, $catskill-white 100%);
  background: linear-gradient(45deg, $geyser 0%, $catskill-white 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='$geyser', endColorstr='$catskill-white', GradientType=1 );
}

// Media query mixins

@mixin tablet {
  @media (min-width: #{$brk-tablet}) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: #{$brk-desktop}) {
    @content;
  }
}

// Placeholders

%center {
  margin: 0 auto;
}

%container {
  @extend %center;
  width: 95%;
}

%p-5 {
  padding: em(5px);
}

%p-15 {
  padding: em(15px);
}

%m-top {
  margin-top: em(25px);
}

%m-bottom {
  margin-bottom: em(25px);
}

%b-radius {
  border-radius: 2.5px;
}

%b-shadow {
  box-shadow: 0 0 18px $color-shadow;
}

%card {
  @extend %b-radius;
  @extend %b-shadow;
  background: $color-background;
  padding: em(15px) 2.5%;
}
